.outerContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #1A1A1D;
  }
  
  .container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #FFFFFF;
    border-radius: 8px;
    height: 60%;
    width: 35%;
  }
  
  @media (min-width: 320px) and (max-width: 480px) {
    .outerContainer {
      height: 100%;
    }
  
    .container {
      width: 100%;
      height: 100%;
    }
  }
  
  @media (min-width: 480px) and (max-width: 1200px) {
    .container {
      width: 60%;
    }
  }
  /* =============== Upgrading ========== */
    .scrollable-messages {
      height: calc(100% - 70px); /* subtract height of input component */
      overflow-y: auto;
    }
    .scrollable-messages::-webkit-scrollbar {
      width: 10px;
      background-color: #F5F5F5;
    }
    
    .scrollable-messages::-webkit-scrollbar-thumb {
      border-radius: 10px;
      background-color: #284478;
    }
    
    .scrollable-messages::-webkit-scrollbar-thumb:hover {
      background-color: #555555;
    }
    .scrollable-messages.hide-scrollbar::-webkit-scrollbar {
      width: 0;
      background-color: transparent;
    }
    
    